/*-----------------*/
/*--- F O N T S ---*/
/*-----------------*/

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

/*-------------------------------------------*/
/*--- S I T E W I D E   C O N S T A N T S ---*/
/*-------------------------------------------*/

:root {
    /*--- standard colours ---*/
    --black: 0, 0, 0;
    --grey-875: 32, 32, 32;
    --grey-750: 64, 64, 64;
    --grey-625: 96, 96, 96;
    --grey-500: 128, 128, 128;
    --grey-375: 160, 160, 160;
    --grey-250: 192, 192, 192;
    --grey-125: 224, 224, 224;
    --white: 255, 255, 255;

    /*--- design specific ---*/
    --grey: 83, 83, 83;     /* #535353 - Medium gray */
    --grey-dark: 34, 34, 34;       /* #222222 - Dark gray */
    --teal: 38, 102, 90;           /* #26665A - Teal/blue-green */
    --green: 51, 101, 16;          /* #336510 - Dark green */
    --grey-light: 250, 250, 250;    /* #FAFAFA - Light gray */
    --grey-light-2: 217, 217, 217;    /* #D9D9D9 - Light gray 2 */

    /*--- fonts ---*/
    --font-family: 'Montserrat', sans-serif;

    /*--- font sizes ---*/
    --xl: 60rem;    /* Extra Large - H1 headings, main titles */
    --l: 30rem;     /* Large - H2 headings, subtitles */
    --m: 24rem;     /* Medium - H3 headings, accents */
    --s: 16rem;     /* Small - body text, buttons */
    --xs: 14rem;    /* Extra Small - small text, captions */

    -raduis-280: 280rem;
    -raduis-200: 200rem;
    -raduis-180: 180rem;
    -raduis-140: 140rem;
    -raduis-50: 50rem;
    -raduis-30: 30rem;


}


/*-------------------*/
/*--- G L O B A L ---*/
/*-------------------*/

* {
    margin: 0;
    padding: 0;
    line-height: 1;
    box-sizing: border-box;
}

html { scroll-behavior: smooth; }

html, body {
    width: 100%;
    height: 100%;
}

body {
    font-family: var(--font-family);
    font-size: var(--s);
    font-weight: 400;
    color: rgb(var(--grey-dark));
}

.container {
    width: 1140rem;
    max-width: 90vw;
    margin: 0 auto;
}



/*---------------------*/
/*--- H E L P E R S ---*/
/*---------------------*/




/*-----------------------*/
/*--- D E F A U L T S ---*/
/*-----------------------*/

h1 {
    font-size: var(--xl);
    font-weight: 600;
    color: rgb(var(--grey));
}

h2 {
    font-size: var(--l);
    font-weight: 600;
    color: rgb(var(--grey));
}

h3 {
    font-size: var(--m);
    color: rgb(var(--grey));
    font-weight: 600;
}

h4 { 
    font-size: var(--s);
    color: rgb(var(--grey-dark));
    font-weight: 700;
}

p, li, ol { font-weight: 400; }

a {
    color: rgb(var(--grey));
    text-decoration: none;
}

strong {
    font-weight: 600;
}
ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

/*---------------------*/
/*--- B U T T O N S ---*/
/*---------------------*/

button {
    appearance: none;
    border: none
}

.button {
    position: relative;
    display: inline-flex;
    min-height: 50rem;
    min-width: 200rem;
    padding: 0rem 10rem;
    align-items: center;
    justify-content: center;
    font-size: var(--s);
    font-weight: 500;
    line-height: 1;
    text-decoration: none;
    border-radius: 50rem;
    cursor: pointer;
    transition: all ease-in-out .25s;
    text-transform: uppercase;
    letter-spacing: 1.6rem;
}

.button:hover { text-decoration: none; }

.button.-v1 {
    color: rgb(var(--teal));
    border: 1px solid rgb(var(--teal));
}

.button.-v2 {
    color: rgb(var(--white));
    background-color: rgb(var(--teal));
}

.button.-v1:hover {
    color: rgb(var(--white));
    background-color: rgb(var(--teal));
}

.button.-v2:hover {
    color: rgb(var(--white));
    background-color: rgb(var(--green));
}


/*-------------------*/
/*--- H E A D E R ---*/
/*-------------------*/
.header-menu {
    background: rgba(var(--grey-light));
    padding-top: 38rem;
    padding-bottom: 38rem;
}
.header-menu ul {
    display: flex;
    align-items: center;
    justify-content: center;
}
.header-menu ul li {
    margin-left: 55rem;
    margin-right: 55rem;
}
.header-menu ul li a {
    text-transform: uppercase;
    font-weight: 500;
    padding-top: 2rem;
    padding-bottom: 2rem;

}
.header-menu ul li a:hover {
    color: rgb(var(--teal));
    border-bottom: 2px solid rgb(var(--teal)); 
}
.header-top {
    padding-top: 20rem;
    padding-bottom: 20rem;

}
.header-top .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
} 
.logo {
    z-index: 101;
    position: relative;
}
.logo img {
    width: 470rem;
}
.search-button {
    background: transparent;
    position: absolute;
    top: 50%;
    right: 15rem;
    transform: translateY(-50%);
    cursor: pointer;
}
.search-wrapper {
    position: relative;
    transition: all 0.3s ease;
}
.search-input {
    width: 0;
    height: 50rem;
    padding: 0rem 25rem;
    border: 1px solid rgb(var(--grey-light-2));
    border-radius: 50rem;
    font-size: var(--xs);
    font-weight: 400;
    color: rgb(var(--grey));
    background-color: rgba(var(--white));
    outline: none;
    font-family: var(--font-family);
    letter-spacing: 1.4rem;
    padding-right: 58rem;
    opacity: 0;
    transition: all 0.3s ease;
}
.search-wrapper.active .search-input {
    width: 320rem;
    opacity: 1;
}
.header-right {
    display: flex;
    align-items: center;
}
.header-icon {
    display: block;
    margin-left: 15rem;
    margin-right: 15rem;
}
.header .button {
    margin-left: 15rem;
}
.menu-btn {
    display: none;
    position: relative;
    z-index: 101;
}
.header-icon {
    z-index: 101;
    position: relative;
}
.header-menu ul li:last-child {
    display: none;
}

/*-----------------*/
/*--- H E R O   S L I D E R ---*/
/*-----------------*/

.hero-slider {
    width: 100%;
    height: 424rem;
    overflow: hidden;
}

.hero-slider .carousel {
    width: 100%;
    height: 100%;
}

.hero-slider .carousel-cell {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-slider .carousel-cell img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

/* Page dots styling */
.hero-slider .flickity-page-dots {
    position: absolute;
    bottom: 28rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
}

.hero-slider .flickity-page-dots .dot {
    width: 10rem;
    height: 10rem;
    margin: 0 6rem;
    background: transparent;
    border-radius: 50%;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
    border: 1px solid rgb(var(--white));
    opacity: 1;
}

.hero-slider .flickity-page-dots .dot.is-selected {
    background: rgb(var(--white));
}